%visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
%btn {
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: inherit;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.28;

  border-radius: 10px;
}
